import type { BreadcrumbList, ListItem, WithContext } from 'schema-dts';

export interface GetBreadcrumbLdProps
{
	/**
	 * 이름
	 */
	name: string;

	/**
	 * URL
	 */
	url: string;
}

/**
 * BreadcrumbList JSON-LD 반환 메서드
 *
 * @param {GetBreadcrumbLdProps[]} breadcrumbs: GetBreadcrumbLdProps[]
 * @param {Partial<BreadcrumbList>} override: BreadcrumbList
 *
 * @returns {BreadcrumbList} BreadcrumbList
 */
export function getBreadcrumbListLd(breadcrumbs: GetBreadcrumbLdProps[], override?: Partial<BreadcrumbList>): BreadcrumbList
{
	return {
		'@type': 'BreadcrumbList',
		itemListElement: breadcrumbs.map<ListItem>((breadcrumb, idx) => ({
			'@type': 'ListItem',
			item: idx + 1 === breadcrumbs.length ? undefined : breadcrumb.url,
			name: breadcrumb.name,
			position: idx + 1
		})),
		...override
	};
}

/**
 * Context BreadcrumbList JSON-LD 반환 메서드
 *
 * @param {GetBreadcrumbLdProps[]} breadcrumbs: GetBreadcrumbLdProps[]
 * @param {Partial<BreadcrumbList>} override: BreadcrumbList
 *
 * @returns {BreadcrumbList} Context BreadcrumbList
 */
export function getBreadcrumbListLdWithContext(breadcrumbs: GetBreadcrumbLdProps[], override?: Partial<BreadcrumbList>): WithContext<BreadcrumbList>
{
	const jsonld = getBreadcrumbListLd(breadcrumbs, override);

	return {
		'@context': 'https://schema.org',
		...jsonld
	};
}