import { owner } from '../util';

import type { Person, WithContext } from 'schema-dts';

export type PersonForLd = Exclude<Person, string>;
export type GetPersonLdProps = Partial<PersonForLd>;

/**
 * Person JSON-LD 반환 메서드
 *
 * @param {GetPersonLdProps} override: GetPersonLdProps
 *
 * @returns {PersonForLd} PersonForLd
 */
export function getPersonLd(override?: GetPersonLdProps): PersonForLd
{
	return {
		'@type': 'Person',
		alternateName: owner.nickname,
		description: '끊임없이 배우고 성장하는 개발자',
		email: `mailto:${owner.email}`,
		gender: 'Male',
		image: owner.image,
		jobTitle: 'Frontend Web Developer',
		knowsLanguage: [
			{
				'@type': 'Language',
				name: 'Korean'
			},
			{
				'@type': 'Language',
				name: 'English'
			}
		],
		name: owner.name,
		nationality: 'KR',
		sameAs: [
			'https://api.itcode.dev',
			'https://blog.itcode.dev',
			'https://project.itcode.dev',
			owner.social.github.link,
			owner.social.linkedin.link
		],
		url: 'https://itcode.dev',
		worksFor: {
			'@type': 'Organization',
			name: '아하앤컴퍼니',
			url: 'https://a-ha.io'
		},
		...override
	};
}

/**
 * Context Person JSON-LD 반환 메서드
 *
 * @param {GetPersonLdProps} override: GetPersonLdProps
 *
 * @returns {WithContext<PersonForLd>} Context Person
 */
export function getPersonLdWithContext(override?: GetPersonLdProps): WithContext<PersonForLd>
{
	const jsonld = getPersonLd(override);

	return {
		'@context': 'https://schema.org',
		...jsonld
	};
}