import { parseLocalDate } from '../util';

import { getPersonLd } from './person';

import type { BlogPosting, WithContext } from 'schema-dts';

export interface GetBlogPostingLdProps
{
	/**
	 * 제목
	 */
	title: string;

	/**
	 * 부제목
	 */
	subtitle: string;

	/**
	 * 이미지
	 */
	image: string;

	/**
	 * 키워드
	 */
	keywords: string[];

	/**
	 * URL
	 */
	url: string;

	/**
	 * 작성일
	 */
	publishedAt: number;

	/**
	 * 수정일
	 */
	modifiedAt?: number;
}

/**
 * BlogPosting JSON-LD 반환 메서드
 *
 * @param {GetBlogPostingLdProps} param0: GetBlogPostingLdProps
 * @param {Partial<BlogPosting>} override: BlogPosting
 *
 * @returns {BlogPosting} BlogPosting
 */
export function getBlogPostingLd({ title, subtitle, image, keywords, url, publishedAt, modifiedAt }: GetBlogPostingLdProps, override?: Partial<BlogPosting>): BlogPosting
{
	const publishedDate = parseLocalDate(publishedAt);
	const modifiedDate = parseLocalDate(modifiedAt);

	const person = getPersonLd();

	return {
		'@type': 'BlogPosting',
		author: person,
		dateCreated: `${publishedDate.year.text}-${publishedDate.month.text}-${publishedDate.date.text}`,
		dateModified: `${modifiedDate.year.text}-${modifiedDate.month.text}-${modifiedDate.date.text}`,
		datePublished: `${publishedDate.year.text}-${publishedDate.month.text}-${publishedDate.date.text}`,
		description: subtitle,
		headline: title,
		image,
		keywords: keywords.join(', '),
		mainEntityOfPage: {
			'@id': url,
			'@type': 'WebPage',
			url
		},
		...override
	};
}

/**
 * Context BlogPosting JSON-LD 반환 메서드
 *
 * @param {GetBlogPostingLdProps} param0: GetBlogPostingLdProps
 * @param {Partial<BlogPosting>} override: BlogPosting
 *
 * @returns {WithContext<BlogPosting>} Context BlogPosting
 */
export function getBlogPostingLdWithContext(props: GetBlogPostingLdProps, override?: Partial<BlogPosting>): WithContext<BlogPosting>
{
	const jsonld = getBlogPostingLd(props, override);

	return {
		'@context': 'https://schema.org',
		...jsonld
	};
}