import type { Meta, Parameters } from '@storybook/nextjs-vite';

type GetArgTypeArg = 'children' | 'className' | 'id';

export enum SubCategory
{
	PROP = 'Property',
	EV = 'Event'
}

/**
 * 기 정의된 Storybook ArgType 반환 메서드
 *
 * @param {GetArgTypeArg} arg: GetArgTypeArg
 * @param {string} category: 카테고리
 *
 * @returns {ExtractRecordValue} InputType
 */
export function getArgType(arg: GetArgTypeArg, category: string): ExtractRecordValue<Meta['argTypes']>
{
	switch (arg)
	{
		case 'children': return {
			control: { disable: true },
			description: '자식 노드',
			table: {
				category,
				subcategory: SubCategory.PROP,
				type: { summary: 'React.ReactNode' }
			}
		};

		case 'className': return {
			control: { disable: true },
			description: '클래스',
			table: {
				category,
				subcategory: SubCategory.PROP,
				type: { summary: 'string' }
			},
			type: 'string'
		};

		default: return {
			control: { disable: true },
			description: '태그 고유 ID',
			table: {
				category,
				subcategory: SubCategory.PROP
			},
			type: 'string'
		};
	}
}

/**
 * 파라미터 반환 메서드
 *
 * @param {string} subtitle: 서브 타이틀
 * @param {string | string[]} body: 내용
 * @param {Parameters} parameters: 결합할 파라미터
 *
 * @returns {Parameters} 파라미터
 */
export function getParameters(subtitle: string, body?: string | string[], parameters?: Parameters): Parameters
{
	return {
		...parameters,
		componentSubtitle: subtitle,
		docs: { description: { component: Array.isArray(body) ? body.join('<br />') : body } }
	};
}